import SendEmail from "@/js/recruit/send-email";

export default {
  // Send email
  async sendEmail(type) {
    // Change button status
    this.send_email_button_status = "Sending";

    // Rest call to send an email
    let response = await SendEmail.send(this.domain_id, {
      to: this.share_to_email,
      job_id: this.row_data.id,
      job_url: type == "view-job" ? window.location.href : window.location.href + `/${this.row_data.id}`,
    });

    // Check send email response
    if (response) {
      this.send_email_button_status = "Sent";
      this.send_email_status = true;
      setTimeout(() => {
        this.send_email_button_status = "Send";
        this.send_email_status = false;
      }, 2000);
    }
  },
};
