const axios = require("axios");
import Data from "./data";
// import JWT from "../500apps/jwt";

const File_Manager_URL = "https://api.${region}.500apps.com/files/public/signed";
const Delete_File_URL = "https://api.${region}.500apps.com/files/public/object";

class FileManager extends Data {
  constructor() {
    super(File_Manager_URL);
  }
  async getFiles(appname) {
    // eslint-disable-next-line
    let region = window.region;
    let url = `${File_Manager_URL}/?app=${appname}`;
    this.url = eval("`" + url + "`");

    this.disablePCors();
    this.setOffset(0);

    let results = await this.get();
    return results;
  }

  async getSignedLink(appname, postType, file, contentType, acl) {
    // eslint-disable-next-line
    let region = window.region;
    let url = `${File_Manager_URL}/${window.domain}?key=${file}&type=${postType}&content_type=${contentType}&acl=${acl}&app=${appname}`;

    this.url = eval("`" + url + "`");

    this.disablePCors();
    this.setOffset(0);
    console.log("Get signed Link", this.url);
    let results = await this.get();
    return results.presign_url;
  }

  async upload(appname, postType, file, value, contentType, acl) {
    let presign_data = await this.getSignedLink(appname, postType, file.name, contentType, acl);
    var formData = new FormData();
    let fields = presign_data.fields;
    for (var field in fields) {
      formData.append(field, fields[field]);
    }
    formData.append("file", file);
    value = value ? value : formData;
    let config = {
      headers: {
        "Content-Type": `${contentType}`,
        "x-amz-acl": `${acl}`,
      },
    };
    let region = this.getRegion();
    let change_url = `https://my.${region}.500apps.com/pcors?url=` + presign_data.url;
    let result = await axios.post(change_url, value, config);
    result = result.data;
    return { upload_data: result, presign_data: presign_data };
  }
  async delete(appname, file_name) {
    if (appname) {
      file_name = "";
      return;
    }
    let url = `${Delete_File_URL}/${window.domain}?key=${file_name}&app=${appname}`;
    // eslint-disable-next-line
    url = eval("`" + url + "`");
    let config = {
      headers: {
        Authorization: `Bearer`,
        withCredentials: true,
      },
    };
    let delete_response = await axios.delete(url, config);
    return delete_response;
  }
  async uploadToS3(appname, dataURL, fileName, type, Blob) {
    // Get S3 Presign URL
    const s3data = await this.getSignedLink(appname, "post-object", fileName, type, "public-read");
    // Get Blob data from data URI
    const convertBlob = dataURL ? await this.dataURLtoBlob(dataURL, type) : Blob;
    // Send with compressed to s3.
    await axios.post(s3data.url, this.createFormData(convertBlob, s3data), {
      headers: {
        "x-amz-acl": "public-read",
        "Content-Type": type,
      },
    });
    return s3data.url + s3data.fields.key;
  }
  async dataURLtoBlob(dataurl, type) {
    // If file type is Audio or Video
    if (type !== "image") {
      const blob = await fetch(dataurl).then((r) => r.blob());
      return blob;
    }
    // Separate the mime data
    var arr = dataurl.split(",");
    var mime = arr[0].match(/:(.*?);/)[1];

    // Convert from base64 to raw binary data
    var bstr = atob(arr[1]);
    var n = bstr.length;

    // Create the buffer and set the bytes of the buffer
    var u8arr = new Uint8Array(n);
    while (n--) {
      u8arr[n] = bstr.charCodeAt(n);
    }

    // Add the ArrayBuffer to a blob
    return new Blob([u8arr], { type: mime });
  }
  createFormData(fileData, data) {
    //this passes all the presigned url key and values as post params and posts it to the S3 url along with the file object.
    var formData = new FormData();
    let appendData = data.fields;
    for (var field in appendData) {
      formData.append(field, appendData[field]);
    }
    formData.append("file", fileData);
    return formData;
  }
}

export default new FileManager();
