import DefaultData from "@/js/recruit/default-data";
import Data from "@/js/core/data";

const Recaptcha_Server = "https://api.${region}.500apps.com";

export default {
  // Get Recaptcha keys
  async getRecaptchaKeys(shortcut_host, schema_name, domain_id, tenant, group_id) {
    var recaptcha_keys = {
      recaptcha_site_key: "",
      recaptcha_secret_key: "",
    };
    let host = window.location.host;
    var type = host.includes(".500apps.org") ? "internal" : host.includes(`${shortcut_host}`) ? "shortcut" : "cname";
    if (type == "internal") {
      // Assign default site_key and secret_key
      recaptcha_keys["recaptcha_site_key"] = await DefaultData.recaptcha_500apps_org_keys["site_key"];
      recaptcha_keys["recaptcha_secret_key"] = await DefaultData.recaptcha_500apps_org_keys["secret_key"];
    } else if (type == "shortcut") {
      // Assign shortcutURL default site_key and secret_key
      recaptcha_keys["recaptcha_site_key"] = await DefaultData.recaptcha_recruithire_shortcut_keys["site_key"];
      recaptcha_keys["recaptcha_secret_key"] = await DefaultData.recaptcha_recruithire_shortcut_keys["secret_key"];
    } else {
      // Get recaptcha keys by schema_name, domain_id, tenant and group_id or project_id
      let url = group_id
        ? eval("`" + `${Recaptcha_Server}/${schema_name}/${domain_id}/${tenant}/${group_id}/captcha` + "`")
        : eval("`" + `${Recaptcha_Server}/${schema_name}/${domain_id}/${tenant}/captcha` + "`");
      let plugins_data = new Data(url);
      plugins_data.disablePCors();
      let response = await plugins_data.get();

      // Check response and Assign site_key and secret_key
      if (response && response.length) {
        recaptcha_keys["recaptcha_site_key"] = JSON.parse(response[0].recaptcha_data)["site-key"];
        recaptcha_keys["recaptcha_secret_key"] = JSON.parse(response[0].recaptcha_data)["secret-key"];
      }
    }
    return recaptcha_keys;
  },
};
