import Data from "@/js/core/data";

const Send_Email_Server = "https://api.${region}.500apps.com/recruit";

class SendEmail {
  // Send email
  async send(domain_id, form_data) {
    let url = `${Send_Email_Server}/${domain_id}/send/mail`;
    let data = new Data(url);
    data.disablePCors();
    let send_response = await data.multi_call(form_data);
    return send_response;
  }
}
export default new SendEmail();
