import Data from "@/js/core/data";

const Job_Details_Server = "https://api.${region}.500apps.com/recruit";
const Shortcut_Server = "https://api.500apps.com/shortcuts/shortcut";
const Shortcut_Region_Server = "https://api.${region}.500apps.com/shortcuts/shortcut";

class JobDetails {
  // Get domain level jobs
  getDomainJobs(domain_id, sub_domain) {
    let url = eval("`" + `${Job_Details_Server}/${domain_id}/${sub_domain}` + "`");
    let data = new Data(url);
    data.disablePCors();
    return data.get();
  }
  getActiveJobs(domain_id, group_id) {
    let url = eval("`" + `${Job_Details_Server}/${domain_id}/${group_id}/jobs` + "`");
    let data = new Data(url);
    data.disablePCors();
    return data.get();
  }
  getFilteredJobs(domain_id, group_id, params, is_sub_domain) {
    var url = "";
    if (is_sub_domain) url = eval("`" + `${Job_Details_Server}/${domain_id}/${is_sub_domain}` + `${params}` + "`");
    else url = eval("`" + `${Job_Details_Server}/${domain_id}/${group_id}/jobs` + `${params}` + "`");
    let data = new Data(url);
    data.disablePCors();
    return data.get();
  }
  getJobDetailsById(domain_id, job_id) {
    let url = eval("`" + `${Job_Details_Server}/${domain_id}/jobs/${job_id}` + "`");
    let job_data = new Data(url);
    job_data.disablePCors();
    return job_data.get();
  }

  async getShortcutData(name) {
    let url;
    if (window.region && (window.region == "dev" || window.region == "qa")) url = eval("`" + ` ${Shortcut_Region_Server}/${name}` + "`");
    else url = eval("`" + ` ${Shortcut_Server}/${name}` + "`");
    let data = new Data(url);
    data.disablePCors();
    return data.get();
  }
}
export default new JobDetails();
